@import url("https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap");

* {
  font-family: "Open Sans", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
  font-variation-settings: "wdth" 100;
}

body {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  grid-template-rows: 100vh;
  margin: 0;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
}

main {
  padding: 2rem;
  grid-column: 2 / 3;
  overflow-y: scroll;
}

footer {
  grid-column: 3 / 4;
  text-align: right;
  align-content: end;
  list-style-type: none;
  font-size: small;
  color: gray;
  ul {
    margin: 1rem;
    list-style-type: none;
  }
}

@media (min-width: 481px) and (max-width: 900px) {
  main {
    grid-column: 1 /3;
    padding: 1rem;
  }
}

@media (max-width: 480px) and (orientation: portrait), (max-width: 600px) and (orientation: landscape) {
  main {
    grid-column: 1 /4;
    padding: 0.6rem;
  }
  footer {
    grid-column: 1 / 4;
  }
}

header {
  padding: 1rem 0;
  h1 {
    font-size: 22px;
    font-weight: 400;
    display: inline;
  }
  img {
    vertical-align: text-bottom;
    cursor: not-allowed;
  }
}
